export default {
	components: {},
	methods: {
		scroll_to_about_me() {
			document.getElementsByClassName('home/about-me')[0].scrollIntoView({
				behavior: 'smooth'
			});
		},
		type() {
			let i = 0;
			const h1 = document.querySelector('h1');
			let txt = h1.innerText;
			const height = h1.offsetHeight;
			h1.style.setProperty('height',`${height}px`);
			h1.innerText = '';
			console.log(height);
			const speed = 40;
			const comma_delay = 500;
			console.log(h1.innerText);
			function typeWriter() {
				if (i < txt.length) {
					const char = txt.charAt(i);
					h1.innerHTML += char;
					i++;

					if (char === ',') {
						setTimeout(typeWriter, comma_delay);
						return;
					}
					setTimeout(typeWriter, speed);
				}
			}
			setTimeout(typeWriter, 300);
		},
	},
	mounted() {
		this.type();
	}
}
