import Navbar from '@/core_structure/Navbar/Navbar.vue'
import Footer from '@/core_structure/Footer/Footer.vue'

export default {
	name: 'App',
	components: {
		Navbar,
		Footer,
	},
	mounted() {
		/*window.$(this.$el).find('.scrollbar-macosx').each(function () {
			window.$(this).scrollbar({
				"disableBodyScroll": false,
			});
			//apply_custom_scroll_bar($(this), $(this).next().next());
		});*/

	},

	methods: {
		childMounted() {
			console.log('child mounted', document.getElementsByClassName('tilt'));

			for (let el of document.getElementsByClassName('tilt')) {

				const handleMove = (e) => {
					const height = el.clientHeight;
					const width = el.clientWidth;
					const xVal = e.layerX;
					const yVal = e.layerY;

					const yRotation = 20 * ((xVal - width / 2) / width);
					const xRotation = -20 * ((yVal - height / 2) / height);

					el.style.transform = 'perspective(500px) rotateX(' + xRotation + 'deg) rotateY(' + yRotation + 'deg)';
				};

				el.addEventListener('mousemove', handleMove);

				el.addEventListener('mouseout', function () {
					el.style.transform = 'perspective(500px) scale(1) rotateX(0) rotateY(0)';
				}, {capture: true});

				el.addEventListener('mousedown', function () {
					el.style.transform = 'perspective(500px) scale(0.9) rotateX(0) rotateY(0)';
				}, {capture: true});

				el.addEventListener('mouseup', function () {
					el.style.transform = 'perspective(500px) scale(1) rotateX(0) rotateY(0)';
				}, {capture: true});
			}
			if (!window.$) {
				return;
			}
			window.$('.scrollbar-macosx').each(function () {
				window.$(this).scrollbar({
					"disableBodyScroll": false,
				});
				window.$(this).scrollTop(0);
				//apply_custom_scroll_bar($(this), $(this).next().next());
			});

		},
	}
}
