import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/home/home.vue'

Vue.use(VueRouter);

const routes = [
	{
		path: '/',
		name: 'Home',
		component: Home,
	},
	{
		path: '/about-me',
		name: 'about-me',
		component: () => import(/* webpackChunkName: "about_me" */ '@/views/about_me/about_me.vue')
	},
	{
		path: '/projects',
		name: 'projects',
		component: () => import(/* webpackChunkName: "projects" */ '@/views/projects/projects.vue')
	},
	{
		path: '/skills',
		name: 'skills',
		component: () => import(/* webpackChunkName: "skills" */ '@/views/skills/skills.vue')
	},
	{
		path: '/contact',
		name: 'contact',
		component: () => import(/* webpackChunkName: "contact" */ '@/views/contact/contact.vue')
	},
	{
		path: '/terms-and-conditions',
		name: 'terms-and-conditions',
		component: () => import(/* webpackChunkName: "terms_and_conditions" */ '@/views/terms_and_conditions/terms_and_conditions.vue')
	},
	{
		path: '/credits',
		name: 'credits',
		component: () => import(/* webpackChunkName: "credits" */ '@/views/credits/credits.vue')
	},
	{
		path: '*',
		name: 'Not found',
		component: () => import(/* webpackChunkName: "not_found" */ '@/views/not_found/not_found.vue')
	},
];

const router = new VueRouter({
	routes,
	mode: "history"
});

function isExternalUrl(input) {
	if (!input || input.match(/^ *$/) !== null) { //isBlank is a function that just checks for empty strings, etc
		return false;
	}

	return new RegExp("^(?:(?:https?|ftp):\\/\\/|\\b(?:[a-z\\d]+\\.))(?:(?:[^\\s()<>]+|\\((?:[^\\s()<>]+|(?:\\([^\\s()<>]+\\)))?\\))+(?:\\((?:[^\\s()<>]+|(?:\\(?:[^\\s()<>]+\\)))?\\)|[^\\s`!()\\[\\]{};:'\".,<>?«»“”‘’]))?").test(input);
}

router.beforeEach((to, from, next) => {
	const extUrl = to.fullPath.startsWith("/") ? to.fullPath.substring(1) : to.fullPath;

	if (isExternalUrl(extUrl)) {
		window.open(extUrl, "_blank");
		return;
	}

	next();
})

export default router
